#DIV_1 {
  border-block-end-color: rgb(171, 176, 181);
  border-block-end-style: solid;
  border-block-end-width: 1px;
  border-block-start-color: rgb(171, 176, 181);
  border-block-start-style: solid;
  border-block-start-width: 1px;
  border-end-end-radius: 4px;
  border-end-start-radius: 4px;
  border-inline-end-color: rgb(171, 176, 181);
  border-inline-end-style: solid;
  border-inline-end-width: 1px;
  border-inline-start-color: rgb(171, 176, 181);
  border-inline-start-style: solid;
  border-inline-start-width: 1px;
  border-start-end-radius: 4px;
  border-start-start-radius: 4px;
  bottom: 0px;
  caret-color: rgb(51, 51, 51);
  color: rgb(51, 51, 51);
  column-rule-color: rgb(51, 51, 51);
  display: flex;
  inset-block-end: 0px;
  inset-block-start: 0px;
  inset-inline-end: 0px;
  inset-inline-start: 0px;
  left: 0px;
  max-block-size: 100%;
  max-height: 100%;
  min-block-size: 53px;
  min-height: 53px;
  min-inline-size: auto;
  min-width: auto;
  padding-block-end: 8px;
  padding-block-start: 8px;
  padding-inline-end: 8px;
  padding-inline-start: 16px;
  perspective-origin: 92px 26.5px;
  position: relative;
  right: 0px;
  text-align: left;
  text-decoration: none solid rgb(51, 51, 51);
  text-emphasis-color: rgb(51, 51, 51);
  text-overflow: ellipsis;
  top: 0px;
  transform-origin: 92px 26.5px;
  user-select: none;
  white-space: nowrap;
  width: 400px;
  border: 1px solid rgb(171, 176, 181);
  border-radius: 4px;
  flex: 1 1 auto;
  flex-flow: column nowrap;
  font: 13px OpenSans, "Open Sans", sans-serif;
  outline: rgb(51, 51, 51) none 0px;
  overflow: hidden;
  padding: 8px 8px 8px 16px;
} /*#DIV_1*/

